.home {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;

  &_text-wrapper {
    display: flex;
    justify-content: center;

    h1 {
      color: var(--yellow-theme-main-color);
      margin: 0px;
      font-weight: 500;
      font-size: 8rem;
      line-height: 10rem;
      position: relative;
      overflow: hidden;
      white-space: nowrap;
      animation: typeingEffectAnimation 2.5s steps(30, end);
    }
  }

  &_contact-me {
    display: flex;
    justify-content: center;
    margin: 15px;
    gap: 20px;


    a{
      text-decoration: none;
    }

    button {
      cursor: pointer;
      padding: 10px 65px;
      font-size: 2rem;
      background: transparent;
      color: var(--yellow-theme-main-color);
      border: 1.5px solid var(--yellow-theme-main-color);
      border-radius: 4px;
      font-weight: 500;

      &:hover {
        background: var(--yellow-theme-main-color);
        color: #000000;
      }
    }
  }
  .socialMedia_links {
    display: flex;
    justify-content: center;
    cursor: pointer;
    margin-top: 50px;
    font-size: 45px;
    background: transparent;
    color: var(--yellow-theme-main-color);
    font-weight: 500;
    gap: 10px;

    > * {
      transition: transform 0.2s ease;
    }

    *:hover {
      transform: scale(1.1);
    }

    > * {
      color: inherit;
    }
  }
}

@media only screen and (max-width: 550px) {
  .home_text-wrapper h1 {
    font-size: 2.5rem;
    line-height: 5rem;
  }

  .home_contact-me button {
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 10px 25px;
    font-size: 1.6rem;
    background: transparent;
    color: var(--yellow-theme-main-color);
    border: 1.5px solid var(--yellow-theme-main-color);
    border-radius: 4px;
    font-weight: 400;
  }
}

@media only screen and (min-width: 551px) and (max-width: 768px) {
  .home_text-wrapper h1 {
    font-size: 4rem;
    line-height: 6rem;
  }
}

@media only screen and (min-width: 769px) and (max-width: 990px) {
  .home_text-wrapper h1 {
    font-size: 6rem;
    line-height: 8rem;
  }
}

@keyframes typeingEffectAnimation {
  from {
    width: 0;
  }

  to {
    width: 60%;
  }
}
